.wp-block-puock-alert {
  border: 1px solid transparent;
  border-radius: 0.25rem;

  .p--text {
    padding: 10px;
    border-radius: 3px;
    color: #ffffff;
    background-color: #767676;
    border-color: #f0f0f0;
  }

  .p--center {
    text-align: center;
  }

  .danger {
    color: #721c24;
    background-color: #f8d7da;
    border-color: #f5c6cb;
  }
}
